.arrow{
  width:32px;
  height: 23px;
  border: none;
  background-image: url("../blocks/main/__series/-navigation/navigation-arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  position: absolute;
  top:50%;
  right: -35px;
  z-index: 99;
  outline: none;
  cursor: pointer;
}