.card{
  width: 164px;
  height: 113px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 13px;
  margin-right: 30px;
}
.card-subtitle{
  font-size: 10px;
  line-height: 12px;
  margin-bottom: 2px;
}
.card-title{
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
}